import * as vscode from 'vscode';
import { $Afc } from './audioPreview';
import { $vfc } from './binarySizeStatusBarEntry';
import { $Efc } from './imagePreview';
import { $Ffc } from './videoPreview';

export function activate(context: vscode.ExtensionContext) {
	const binarySizeStatusBarEntry = new $vfc();
	context.subscriptions.push(binarySizeStatusBarEntry);

	context.subscriptions.push($Efc(context, binarySizeStatusBarEntry));
	context.subscriptions.push($Afc(context, binarySizeStatusBarEntry));
	context.subscriptions.push($Ffc(context, binarySizeStatusBarEntry));
}
